//libraries
import React, {Component} from 'react';
//styles
import classes from './NavMobile.scss';
//components
import NavItem from '../NavItem/NavItem';
import NavBtn from '../NavBtn//NavBtn';

class Navigation extends Component {

  state = {
    hide: true,
    active: false,
  }

  toggleMobile = () => {
    this.setState({
      hide: !this.state.hide,
      active: !this.state.active
    })
  }
 render() {

   let showMenu = classes.Hide;
   this.state.hide ? showMenu = classes.Hide : showMenu = classes.Show
  
   return (
    <nav className={classes.NavMobile}>
      <NavBtn clicked={this.toggleMobile} active={this.state.active}/>
      <div className={classes.NavMenu + " " + showMenu}>
        <ul>    
          <NavItem
            link={`${process.env.PUBLIC_URL}/home`}
            active={classes.Active}
            clicked={this.toggleMobile}>
            Strona główna
          </NavItem> 
          <NavItem
            link={`${process.env.PUBLIC_URL}/fala_uderzeniowa`}
            active={classes.Active}
            clicked={this.toggleMobile}>
            Fala uderzeniowa
          </NavItem>
          <NavItem
            link={`${process.env.PUBLIC_URL}/hitop`}
            active={classes.Active}
            clicked={this.toggleMobile}>
            Hitop
          </NavItem>
          <NavItem
            link={`${process.env.PUBLIC_URL}/tesla_stym`} 
            active={classes.Active}
            clicked={this.toggleMobile}>
            Tesla Stym
          </NavItem>
          <NavItem
            link={`${process.env.PUBLIC_URL}/laser_wysokoenergetyczny`}
            active={classes.Active}
            clicked={this.toggleMobile}>
            Laser wysokoenergetyczny
          </NavItem>
          <NavItem
            link={`${process.env.PUBLIC_URL}/cennik`}
            active={classes.Active}
            clicked={this.toggleMobile}>
          Cennik
          </NavItem>
          <NavItem
            link={`${process.env.PUBLIC_URL}/kontakt`}
            active={classes.Active}
            clicked={this.toggleMobile}>
          Kontakt
          </NavItem>
        </ul>
      </div>
    </nav>
    )
  }
} 

export default Navigation;
